export const POST = 'POST';
export const GET = 'GET';
export const HOST = 'https://shyam-anand.com/videoserver';
export const CHUNK_SIZE = 1 * 1024 * 1024; // 1 MB

/**
 * Class with methods for uploading the file.
 * The video file will be split into CHUNK_SIZE sized chunks, and send to the server.
 * After all the chunks are sent, a GET request is made to get the video url, which
 * is used for playback
 */
export default class FileUploader {
	
	constructor(onComplete, onProgress) {
		this.CHUNK_SIZE = CHUNK_SIZE;

		this.fileChecksum = '';
		this.chunkCount = 0;
		this.uploads = [];
		this.uploadProgress = 0;
		this.progresses = [];

		this.onComplete = onComplete;
		this.onProgress = onProgress;
	}

	getChunks(file) {
		console.log(file.size, "file size");
		let chunks = Math.ceil(file.size/this.CHUNK_SIZE, this.CHUNK_SIZE);
		let chunkArray = []
		for (var i = 0; i < chunks; i++) {
			var offset = i * this.CHUNK_SIZE;
			chunkArray.push(file.slice(offset, offset + this.CHUNK_SIZE));
		}
		this.chunkCount = chunkArray.length;
		console.log(this.chunkCount, `chunks for ${file.name}`);
		return chunkArray;
	}

	upload(file, checksum) {
		this.fileChecksum = checksum;
		let chunks = this.getChunks(file);

		chunks.forEach((chunk, index) => {
			var formData = new FormData();
			formData.append('chunk', chunk, `${file.name}.part${index}`);
			formData.append('checksum', this.fileChecksum);
			this.post(formData, index);
		}, this);
	}

	uploadDone(index) {
		console.log(`part${index} uploaded (${this.progresses[index] * 100} %). total progress: ${this.uploadProgress	}`);
		this.uploads.push({index: true});
		if (this.uploads.length === this.chunkCount) {
			if (this.uploadProgress === 100) {
				console.log("Uploads complete, and progress at 100%")
				this.uploadComplete();
			} else {
				console.warn(`All uploads complete, but progress at ${this.uploadProgress}%`)
			}
		}
	}

	post(data, index) {
		var request = new XMLHttpRequest();
		request.open(POST, `${HOST}/files/parts`, true);
		request.onload = function(e) {
			var xhr = e.target;
			if (xhr.status !== 201) {
				this.onComplete(false, {error: xhr.error});
			} else {
				this.uploadDone(index);
			}
		}.bind(this);
		
		request.upload.onprogress = function(e) {
			var progress = e.loaded / e.total;
			this.setProgress(progress, index);
		}.bind(this);
		
		request.addEventListener('error', function(err) {
			console.log(err, "xhr err");
			this.onComplete(false, {error: 'Unable to send files. Check the host URL and your internet connection.'})
		}.bind(this), true);

		request.responseType = "json";
		try {
			request.send(data);
		} catch (exception) {
			console.log(exception);
		}
	}

	setProgress(progress, index) {
		this.progresses[index] = progress;
		var p = this.progresses.reduce((p1, p2) => p1 + p2, 0);
		this.uploadProgress = (p / this.chunkCount) * 100;
		this.onProgress(this.uploadProgress);
	}

	uploadComplete() {
		var request = new XMLHttpRequest();
		request.open(GET, `${HOST}/files/${this.fileChecksum}`);
		request.responseType = "json";
		request.onload = function(e) {
			var response = e.target.response;
			if (e.target.status === 200) {
				console.log(response, "response");
				var video = `${HOST}/files/play/${response.data}/`;
				this.onComplete(true, {videoUrl: video});
			} else {
				if (typeof response.error === "object" && response.error.details) {
					this.onComplete(false, {error: response.details});
				} else if (response.error) {
					this.onComplete(false, {error: response.message})
				} else {
					this.onComplete(false, {error: response});
				}
			}
		}.bind(this);
		request.send();
	}
}


























